import axios from 'axios'
import { baseUrl } from '@/api/index'
import { Message, Loading } from 'element-ui';
import router from '@/router/index'
import Cookies from 'js-cookie'
/**
 * 
 */
let loading //loading
function startLoading(content) { //Element loading-start 
	loading = Loading.service({
		lock: true,
		text: content ? content : '',
		background: 'rgba(0, 0, 0, 0.7)'
	})
}

function endLoading() { //Element loading-close 
	loading.close()
}

// showFullScreenLoading() tryHideFullScreenLoading() 
// needLoadingRequestCountshowFullScreenLoading needLoadingRequestCount + 1
//tryHideFullScreenLoading()needLoadingRequestCount - 1needLoadingRequestCount 0  loading
let needLoadingRequestCount = 0;
export function showFullScreenLoading() {
	if(needLoadingRequestCount === 0) {
		startLoading()
	}
	needLoadingRequestCount++
}

export function tryHideFullScreenLoading() {
	if(needLoadingRequestCount <= 0) return
	needLoadingRequestCount--
	if(needLoadingRequestCount === 0) {
		endLoading()
	}
}
//host  
//export const getUuid = () => axios.get(host + '/school/backend/login/getUuid');

axios.defaults.timeout = 100000;
axios.defaults.baseURL = baseUrl;
axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded';
//http request 
axios.interceptors.request.use(
	config => {
		const token = Cookies.get('token');
		config.data = config.data;
		config.headers = {
			'Content-Type': 'application/x-www-form-urlencoded'
		}
		if(token) {
			config.params = {'token': token}
		} //cookiejs-cookie
		if (config.showLoading) {showFullScreenLoading()}
		return config;
	},
	error => {
		return Promise.reject(err);
	}
);
//http response 
axios.interceptors.response.use(
	response => {
		//
		if(response.data.code == 401) {
			//cookie token name headeImg
			Cookies.remove('name');
			Cookies.remove('token');
			Cookies.remove('headeImg');
			router.push({
				path: "/login",
				querry: {
					redirect: router.currentRoute.fullPath
				} //
			})
		}
		if (response.config.showLoading) {
		  tryHideFullScreenLoading()
		}
		return response;
	},
	error => {
		return Promise.reject(error)
	}
)
/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url, params = {},config = { showLoading: false }) {
	return new Promise((resolve, reject) => {
		axios.get(url, {
				params: params
			},config, {
				withCredentials: true,
				crossDomain: true
			})
			.then(response => {
				resolve(response.data);
			})
			.catch(err => {
				reject(err)
			})
	})
}

/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data = {},config = { showLoading: false }) {
	return new Promise((resolve, reject) => {
		axios.post(url, data,config)
			.then(response => {
				resolve(response.data);
			}, err => {
				reject(err)
			})
	})
}

/**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url, data = {}) {
	return new Promise((resolve, reject) => {
		axios.patch(url, data)
			.then(response => {
				resolve(response.data);
			}, err => {
				reject(err)
			})
	})
}

/**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url, data = {}) {
	return new Promise((resolve, reject) => {
		axios.put(url, data)
			.then(response => {
				resolve(response.data);
			}, err => {
				reject(err)
			})
	})
}


