<template>
	<div class="inform">
		<div v-show="pageStatus == 1">
			<div class="myMoney">
				<h4> </h4>
				<el-button type="primary" class="release-gao" @click="toAddGao"> <i class="el-icon-edit el-icon--right"></i></el-button>
				<p class="border-p"></p>
				
				<!--<h4> </h4>-->
				<el-row class="list-status">
					<el-col :span="6">
						<span @click="changeStatus(1)" :class=" status==1?'active':'' "></span>
					</el-col>
					<el-col :span="6">
						<span @click="changeStatus(2)" :class=" status==2?'active':'' "></span>
					</el-col>
					<el-col :span="6">
						<span @click="changeStatus(3)" :class=" status==3?'active':'' "></span>
					</el-col>
					<el-col :span="6">
						<span @click="changeStatus(4)" :class=" status==4?'active':'' "></span>
					</el-col>
				</el-row>
				<el-table :data="tableData.slice((curPage-1)*pagesize,curPage*pagesize)" style="width: 100%">
					<el-table-column type="index" width="80" label="" align="center"></el-table-column>
					
					<el-table-column label="" width="180">
						<template slot-scope="scope">
							<span>{{ scope.row.title }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.words_count }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.img_count }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.content }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.original_degree }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.baidu }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.sogou }}</span>
						</template>
					</el-table-column>
					<el-table-column label="360">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.b360 }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.type }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="80">
							<span>{{ scope.row.field }}</span>
						</template>
					</el-table-column>
					<el-table-column label="" width="80">
						<template slot-scope="scope">
							<span>{{scope.row.created_at}}</span>
						</template>
					</el-table-column>
					<el-table-column label="" width="80">
						<template slot-scope="scope">
							<span>{{ scope.row.status }}</span>
						</template>
					</el-table-column>
					<el-table-column label="" width="80">
						<template slot-scope="scope">
							<span>{{scope.row.solicit}}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
						</template>
					</el-table-column>
				</el-table>
				<div class="pages">
					<el-pagination @current-change="currentChange" :current-page="curPage" layout="prev, pager, next" :total="totalNum">
					</el-pagination>
				</div>

			</div>
		</div>

		<div v-show="pageStatus == 2">
			<div class="myMoney">
				<h4>  <i class="el-icon-edit-outline"></i></h4>
			</div>
			<el-row>
				<el-col :span="3">
					<label for=""><span class="input-must">*</span></span></label>
				</el-col>
				<el-col :span="10">
					<el-input v-model="form.title" class="add-title" placeholder=""></el-input>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="3">
					<label for=""><span class="input-must">*</span></span></label>
				</el-col>
				<el-col :span="20">
					<el-checkbox-group v-model="quareForm.type">
						<el-checkbox v-for="item in quareForm.typeBox" :label="item.id" :key="item.id" @change="checkBoxs">{{item.name}}</el-checkbox>
					</el-checkbox-group>
				</el-col>
			</el-row>
			<div>
				<vue-ueditor-wrap v-model="form.msg" :config="myConfig"></vue-ueditor-wrap>
			</div>
			<div style="position: absolute;left: 100%;top: 170px;">
				<el-row>
					<el-button type="success" plain icon="el-icon-check" @click="toSave"></el-button>
				</el-row>
				<el-row>
					<el-button type="primary" plain icon="el-icon-upload2" @click="dialogtoUpload"></el-button>
				</el-row>
				<el-row>
					<el-button type="danger" plain icon="el-icon-close" @click="quit"></el-button>
				</el-row>
			</div>
		</div>

		<el-dialog title=" " :visible.sync="dialogVisible" width="55%" :before-close="handleClose">
			<div>
				<el-row>
					<el-col :span="3" style="text-align:center">
						<span></span>
					</el-col>
					<el-col :span="5">
						<span>{{articlemarketForm.words}} </span>
					</el-col>
					<el-col :span="3">
						<span></span>
					</el-col>
					<el-col :span="5">
						<span>{{gaoForm.img}} </span>
					</el-col>
					<el-col :span="3">
						<span></span>
					</el-col>
					<el-col :span="5">
						<el-tag>{{articlemarketForm.category}}</el-tag>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="6" style="text-align:center">
						<span>:</span>
						<span>{{articlemarketForm.baidu*100}} %</span>
					</el-col>
					<el-col :span="6" style="text-align:center">
						<span>:</span>
						<span>{{articlemarketForm.sogou*100}} %</span>
					</el-col>
					<el-col :span="6" style="text-align:center">
						<span>360:</span>
						<span>{{articlemarketForm[360]*100}} %</span>
					</el-col>
					<el-col :span="6" style="text-align:center">
						<span>:</span>
						<span>{{articlemarketForm.guge}} %</span>
					</el-col>
				</el-row>

				<p style="text-align: center;font-size: 18px;color:#ff8547;"></p>

				<div style="width:95%;margin:0 auto;">
					<el-row>
						<el-col :span="4">
							<span style="font-size: 14px; color: #696969;">&nbsp;&nbsp;</span>
							<img :src="gaoForm.happyImg" style="width: 50px;position: relative;top: 15px;" />
							<el-tag type="danger" style="position: relative;top: 20px;left: 65px;">{{parseInt(articlemarketForm.positive_prob*100)}}%</el-tag>
						</el-col>
						<el-col :span="15">

							<p class="emation-tiao"></p>
							<span class="emation-unhappy transAll" :style="{width:(100-articlemarketForm.positive_prob*100)+'%'}"></span>
						</el-col>
						<el-col :span="4" style="margin-left: 15px;">
							<img :src="gaoForm.unhappyImg" style="width: 50px;position: relative;top: 15px;" />
							<span style="font-size: 14px; color: #696969;"></span>
							<el-tag type="danger" style="position: relative;top: 20px;">{{(100-parseInt(articlemarketForm.positive_prob*100))}}%</el-tag>
						</el-col>
					</el-row>

				</div>

				<br />
				<el-row>
					<el-col :span="4" style="text-align:center">
						<label for=""><span class="input-must">*</span>:</label>
					</el-col>
					<el-col :span="6">
						<el-input v-model="quareForm.price" placeholder=""></el-input>
					</el-col>
					<el-col :span="2">
						<span style="margin-left:10px;"></span>
					</el-col>
				</el-row>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="handleClose"> </el-button>
				<el-button type="primary" @click="toUpload"> </el-button>
			</span>
		</el-dialog>
		<!-- <div v-show="pageStatus == 3"></div> -->
	</div>
</template>

<script>
	import VueUeditorWrap from 'vue-ueditor-wrap'
	import { baseUrl } from '@/api/index.js' //
	import happy from '@/assets/images/happy.png'; //
	import unhappy from '@/assets/images/unhappy.png'; //
	import qs from 'qs' //
	export default {
		components: {
			VueUeditorWrap
		},
		data() {
			return {
				dialogVisible: false, //
				pageStatus: 1, //1 2,3
				status: 1, //lab
				getList_url: '/api/article/mylist',
				saveWork: '/api/article/add',
				getCategory: '/api/fields/list', //
				curPage: 1, //
				pagesize: 10, //10
				totalNum: 0, //
				listType:'',  //1 2 : 3
				tableData: [],
				form: {
					msg: '',
					title: ''
				},
				quareForm: {
					typeBox: [],
					type: [],
					checkBoxPre: [], //
					price: 50,
				},
				gaoForm: {
					num: 120,
					img: 10,
					baidu: 10,
					sogou: 30,
					b360: 50,
					bchrome: 70,
					happyImg: happy,
					unhappyImg: unhappy,
				},
				articlemarketForm: {
					//

				},
				myConfig: {
					// ,
					//		            serverUrl: baseUrl+'ueditor/server?action=config&noCache=1542597685533',
//					serverUrl: '/api/ueditor/server',
					serverUrl:'http://api.zimeiku.com/api/ueditor/server',
					// UEditor,index.html
					UEDITOR_HOME_URL: './static/UEditor/',
					// 
					autoHeightEnabled: false,
					// 
					initialFrameHeight: 540,
					// 
					initialFrameWidth: '100%',
					// 
					enableAutoSave: false
				}
			}
		},
		mounted: function() {
			//
			this.getList();
			//
			this.getCategoryFun();
		},
		methods: {
			//
			handleClose(done) {
				let that = this;
				this.$confirm('').then(_ => {
					done();
					//
					that.toSave();
				}).catch(_ => {
					//
					that.dialogVisible = false;
				});
			},
			getList: function(inx) {
				let that = this;
				that.listType = inx;
				that.$fetch(that.getList_url + '?page=' + that.curPage + '&pagesize=' + that.pagesize+'&type='+that.listType, {
					showLoading: true
				}).then((response) => {
					if(response.data.list.length > 0) {
						for(let i = 0; i < response.data.list.length; i++) {
							response.data.list[i].created_at = response.data.list[i].created_at.substring(0, 10);
						}
						that.tableData = response.data.list;
						that.totalNum = response.data.count;
					}
				})
			},
			getCategoryFun: function() {
				//
				let that = this;
				that.$fetch(that.getCategory).then((response) => {
					if(response.data.list) {
						that.quareForm.typeBox = response.data.list;
					}
				}, {
					showLoading: false
				})
			},
			currentChange: function(curPage) {
				this.curPage = curPage;
				console.log(this.curPage) //
			},
			handleEdit(index, row) {
				console.log(index, row);
			},
			handleDelete(index, row) {
				console.log(index, row);
			},
			changeStatus: function(index) {
				let that = this;
				that.status = index;
				//
				if(index-1==0){
					that.getList();
				}else{
					that.getList(index-1);
				}
				
			},
			//
			toAddGao: function() {
				this.pageStatus = 2;
			},
			toSave: function(){
				// 1 2 : 3
				let that = this;
				if(that.form.title) {
					if(that.quareForm.type.length > 0) {
						if(that.form.msg.length > 0) {
							let addData = qs.stringify({
								title: that.form.title,
								field: that.quareForm.type[0],
								content: that.form.msg,
								type: 3, //1 2 : 3
								status: 1, //:1,0
							})
							that.$post(that.saveWork, addData, {
								showLoading: true
							})
							.then(function(response) {
								if(response.code==0){
									that.$message({
							          	message: '',
							          	type: 'success'
							        });
							        that.quit();
								}
							}).catch(function(error) {
								console.log(error);
							});
							
						} else {
							this.$message.error('')
						}
					} else {
						that.$message.error('');
					}
				} else {
					that.$message.error('!');
					return;
				}
			},
			//
			dialogtoUpload() {
				let that = this;
				if(that.form.title) {
					if(that.quareForm.type.length > 0) {
						if(that.form.msg.length > 0) {
							this.$post('/api/article/check', `content=${that.form.msg}`, {
								showLoading: true
							}).then(function(res) {
								that.dialogVisible = true;
								that.articlemarketForm = res.data.list;
								//
								let val_baidu = parseInt(that.articlemarketForm.baidu * 1000);
								let val_sogou = parseInt(that.articlemarketForm.sogou * 1000);
								let val_360 = parseInt(that.articlemarketForm[360] * 1000);
								that.articlemarketForm.guge = parseInt((val_baidu + val_sogou + val_360) / 30);
							}).catch(function(res) {
								console.log(res);

								that.dialogVisible = true;
							})
						} else {
							this.$message.error('')
						}
					} else {
						this.$message.error('')
					}
				} else {
					this.$message.error('')
				}
			},
			//
			toUpload: function() {
				let that = this;
				let addData = qs.stringify({
					title: that.form.title,
					field: that.quareForm.type[0],
					content: that.form.msg,
					type: 2, //1 2 : 3
					status: 1, //:1,0
				})
				this.$post('/api/article/add', addData, {
					showLoading: true
				}).then(function(res) {
					if(res.code==0){
						that.$message({
				          	showClose: true,
				         	message: '',
				          	type: 'success'
				        });
				        that.quit();
					}else{
						
					}
					// that.$message(res.data.msg);
					that.dialogVisible = false
				}).catch(function(res) {
					console.log(res);
				})

			},
			//
			checkBoxs: function() {
				let that = this;
				if(that.quareForm.type.length > 1) {
					that.checkBoxPre = [];
					that.checkBoxPre.push(that.quareForm.type[1]);

					that.quareForm.type = that.checkBoxPre;
				} else {
					that.checkBoxPre = that.quareForm.type;
				}
			},
			quit: function() {
				this.checkBoxPre = this.quareForm.type=[];
				this.form.msg = '';
				this.form.title = '';
				this.pageStatus = 1;
			}
		}
	}
</script>

<style scoped="scoped">
	@import url("../../assets/css/user");
	/*.market{
		width:95%;
	}*/
	
	.list-status {
		margin-bottom: 20px;
	}
	
	.list-status span {
		display: inline-block;
		width: 50%;
		cursor: pointer;
		text-align: center;
	}
	
	.list-status .active {
		padding-bottom: 5px;
		border-bottom: solid 2px #409EFF;
	}
	
	.cash {
		font-size: 20px;
		color: #56BCEB;
	}
	
	.myMoney-cash {
		line-height: 60px;
		background-color: #eef1f6;
		padding-left: 10px;
	}
	
	.myMoney {
		margin-bottom: 50px;
	}
	
	.inform h4 {
		display: inline-block;
		border: none;
		position: relative;
		top: 15px;
	}
	
	.border-p {
		border-bottom: 1px solid #ddd;
		margin-bottom: 20px;
	}
	
	.table-expand {
		font-size: 0;
	}
	
	.table-expand label {
		width: 90px;
		color: #99a9bf;
	}
	
	.table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 50%;
	}
	
	.emation-tiao {
		width: 98%;
		height: 20px;
		background-color: #e6a23c;
		display: inline-block;
		border-radius: 10px;
		position: relative;
		top: 30px;
	}
	
	.emation-unhappy {
		display: inline-block;
		height: 20px;
		background-color: #67c23a;
		position: relative;
		z-index: 999;
		top: 6px;
		float: right;
		border-radius: 0 10px 10px 0;
	}
</style>


